<template>
  <div class="container" style="margin-top:80px">
    <div>
    <b-card bg-variant="light" title='Create Product' footer='Product App'>
        <b-form-group
          label-cols-sm="3"
          label="Name:"
          label-align-sm="right"
          label-for="name"
        >
          <b-form-input id="input-name" :state='is_validname' v-model='product.name'></b-form-input>
          <b-form-valid-feedback id='input-name-valid-feedback'>Looks good !</b-form-valid-feedback>
          <b-form-invalid-feedback id='input-name-invalid-feedback'>Invalid name</b-form-invalid-feedback>
          <b-form-text id='input-name-help-text'>What you wanna sell?</b-form-text>
        </b-form-group>

        <b-form-group
          label-cols-sm="3"
          label="Category:"
          label-align-sm="right"
          label-for="category"
        >
          <b-form-select v-model="product.cat_id" required id='category'>
            <option v-for='catg in categories' :value="catg.id">{{catg.name}}</option>
          </b-form-select>
          <b-form-text id='category-help'>What is the category it falls?</b-form-text>
        </b-form-group>

        <b-form-group
          label-cols-sm="3"
          label="Price:"
          label-align-sm="right"
          label-for="price"
        >
          <b-form-input id="price" v-model='product.price' autocomplete='off' :state='is_validprice'></b-form-input>
          <b-form-text id='price-help'>How much you demand for it ?</b-form-text>
        </b-form-group>

        <b-form-group
          label-cols-sm="3"
          label="How Old?"
          label-align-sm="right"
          label-for="input-live"
        >
        <b-row>
        <b-col cols='4'>
        <b-form-select v-model='product.old.years'>
          <option v-for='(i,j) in 6' :value=j>{{j}}</option>
        </b-form-select>
      </b-col>
      <b-form-text>{{(product.old.years>1)?"years":"year"}} and </b-form-text>
      <b-col cols='4'>
      <b-form-select v-model='product.old.months' :state='is_validold'>
        <option value='0'>less than a month</option>
        <option v-for='i in 11' :value=i>{{i}}</option>
      </b-form-select>
    </b-col>
      <b-form-text>{{(product.old.months>1)?"months":"month"}} Old</b-form-text>
      </b-row>
      </b-form-group>
      <b-form-group
        label-cols-sm="3"
        label="Documents I have:"
        label-align-sm="right"
        label-for="docs"
      >
      <b-form-checkbox-group id='checkbox-group-docs' :options='docs' v-model='product.selected_docs'>
      </b-form-checkbox-group>
    </b-form-group>
        <b-form-group
          label-cols-sm="3"
          label="Description:"
          label-align-sm="right"
          label-for="description"
        >
        <b-form-textarea
      id="textarea"
      :state='is_validdscr'
      v-model="product.dscr"
      placeholder="Say Something about the product . . ."
      rows="3"
      max-rows="6"
    ></b-form-textarea>
        <b-button variant='primary' type='submit' class="m-1" @click.prevent.stop='postproduct()' :disabled='banpost'>Create</b-button>
        </b-form-group>
    </b-card>
  </div>
    <pre>{{product}}</pre>
  </div>
</template>
<script>
export default {
  name: 'CreateProduct',
  data(){
    return {

      categories:[],                  //! need to loaded from the God!
      docs:[{text:'Purchase Bill',value:'pb'},
      {text:'Warranty card',value:'wc'},{text:'Gurantee card',value:'gc'}],

      product:{name:'',dscr:'',price:0,cat_id:'',old:{years:0,months:0},selected_docs:[]},              //product attrs
      err_name:'',err_price:'',err_dscr:'',err_cat_id:'',greet:'',error:'',
      banpost:false,
    }
  },
  created(){
    //get the category list , to get religion of the product !
    axios.get('/categories').then(resp=>{
      this.categories=resp.data
      this.banpost=false
    }).catch(err=>{this.error='Check your Internet Connection !'
                    this.banpost=true         //haha ban !
      })


  },
  methods:{
    postproduct(){
      if(this.is_validname&&this.is_validprice&&this.is_validdscr&&this.is_validold){  //everything looks clean!
        axios.defaults.headers.common['Authorization']='Bearer '+this.token
        axios.post('/products',this.product).then(resp=>{
          alert('Ok saved')
          console.log(resp.data)
        }).catch(err=>{alert('something went wrong') })


      }
      else{alert('Check the data you entered !')}
    }
    }
  ,computed:{
    token:function(){
      return this.$store.getters.token
    },
    is_validname:function(){
      return this.product.name.length > 2 ? true : false},
    is_validprice:function(){
      if(this.product.price=='' ||this.product.price==0||this.product.price<0|| isNaN(this.product.price)){return false}
      return true
    },
    is_validdscr(){
      return (this.product.dscr=='')?false:true
    },
    is_validold:function(){                           //atleast months!
      return (this.product.old.months.length)?false:true;
    }
      },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



