<template>
  <div class="container" style="margin-top:80px">
        <b-card bg-variant="light" title='User Login' footer='Product App'>
          <form @submit.prevent='try_login' novalidate='true'>
          <b-form-group
            label-cols-sm="3"
            label="Email:"
            label-align-sm="right"
            label-for="input-name"
          >
            <b-form-input id="input-name" v-model='auth.email' :state='isemail' required size='sm'></b-form-input>
            <b-form-valid-feedback id='input-name-valid-feedback'>Looks good !</b-form-valid-feedback>
            <b-form-invalid-feedback id='input-name-invalid-feedback' v-if='!isemail'>Invalid Email Address</b-form-invalid-feedback>

          </b-form-group>
          <b-form-group
            label-cols-sm="3"
            label="Password:"
            label-align-sm="right"
            label-for="input-name"
          >
            <b-form-input id="input-name" v-model='auth.password' type='password' required size='sm'></b-form-input>
            <b-form-valid-feedback id='input-name-valid-feedback'>Looks good !</b-form-valid-feedback>
            <b-form-invalid-feedback id='input-name-invalid-feedback'>Invalid name</b-form-invalid-feedback>

          </b-form-group>
          <b-form-group
            label-cols-sm="3"
            label-align-sm="right"
            label-for="input-name"
          >
            <button class="btn btn-primary btn-sm">Login</button>
        </b-form-group>
        <span class='text-danger' v-if='err_login'>{{err_login}}</span>
      </form>
        </b-card>

  </div>
  </template>

<script>
export default {
  name: 'login',
  data () {
    return {
      auth:{
        email:'',password:''
      },err_login:'',greet:''
    }
  },
  created(){
  }
  ,methods:{
    try_login(){
      this.err_login=''
      if(this.auth.email==''||this.auth.password==''){
        this.err_login='Enter both Email and Password'
        return ;        //halt exec
      }

      //brand new Vuex checkpoint !
      this.$store.dispatch('TRY_AUTH',this.auth).then(res=>{
          this.$router.push('/')          //Drag me into front gate !
          this.auth.email=''          //forget !!!
          this.auth.password=''
      }).catch((err)=>{
          console.log(err)
        this.err_login=err||'Oh Bad! Try Again !'

      })


    }
  },computed:{
        token:function(){
          return this.$store.getters.token
        },
        isemail(){
        if(this.auth.email.indexOf('@')!=-1){
        return true
        }
        return false
        }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



