<template>
  <div class="container" style="margin-top:80px">
    <div class="card">
    <div class="card-header"><b>Your products</b><button class="btn btn-success btn-sm p-1 rounded float-right" @click='getProductdata'>Add New </button></div>
    <div class="card-body">
      <ul class="list-group">
          <single-product v-if='products.length>0' v-for='(product,index) in products' :product='product' :key='index'></single-product>
          <!-- {{product.name}} -->
          <!-- <span :class='dclass.apprvd' v-if='product.approved' title='This item is ready to sell !'>Approved</span> -->
          <!-- <span :class='dclass.napprvd' v-else title="Waiting for approval , hang on !">Pending</span> -->
          <span v-else>No products in your Bag !</span>
      </ul>
      </div>

    </div>

    <!--  pagination links-->
    <div class="card m-1" v-if='pgntr.total'>
      <a href="" @click.prevent.stop='reloadAPI(pgntr.prev_page)' v-if='pgntr.prev_page' class="mr-auto">&lt;&lt;Prev</a>
      <a href="" @click.prevent.stop='reloadAPI(pgntr.next_page)' v-if='pgntr.next_page' class="ml-auto">Next&gt;&gt;</a>
    </div>
    <!--  pagination end-->
  </div>
</template>

<script>
import singleProduct from './single_product'
import CreateProduct from './CreateProduct'

export default {
  name: 'Dash',
  components:{singleProduct}
  ,data(){
    return {
      error:'',
      products:[],urlhook:'/products',  //defualt first time src
      pgntr:{total:'',current_page:'',prev_page:'',next_page:'',last_page:''},      //Cut all edges!
    }
  }
  ,beforeRouteUpdate(to,from,next){             // stop ! show ur passpost !
    if(!token){             //Oops , bring ur orig. passport on next time!
      next('/login')
    }
    else{next()}                //Welcome back !

  },
  created(){
    this.fetchproducts()
    // console.log(this.products)
   },
  methods:{
    fetchproducts(){
      this.error=''
        axios.defaults.headers.common['Authorization'] = 'Bearer '+this.token;
        axios.get(this.urlhook).then(resp=>{
        this.products=resp.data.data                  //data inside data -- nested again bcoz paginator!
        this.paginator(resp.data)
      }).catch((err)=>{this.error='Oops! Something Went Wrong '})
    },
    getProductdata(){
      this.$router.push('/create')
    },
    paginator(data){  //work on same product bag
        this.pgntr.total=data.total
        this.pgntr.current_page=data.current_page
        this.pgntr.next_page=data.next_page_url
        this.pgntr.prev_page=data.prev_page_url
    },reloadAPI(newlink){
      this.urlhook=newlink               //pagination hacks ;)
      this.fetchproducts()
    }
  },
  computed:{
    token:function(){
      return this.$store.getters.token
    }

  }

  }

</script>

<style>
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



