<template>
  <div class="container" style="margin-top:80px">
    <div class="card" >
      <div class="card-header">Profile</div>
        <div class="card-body">
          <div class='row'><div class="col-md-3"><figure><img src="../assets/logo.png"></figure></div>
          </div>
            <table class="table table-striped" v-if='userdata'>
              <tr><td>Email</td><td><strong>{{userdata.email}}</strong></td><td><a href="#">Edit</a></td></tr>
              <tr><td>Username</td><td><strong>{{userdata.username}}</strong></td><td><a href="#">Edit</a></td></tr>
            </table>
            <!-- <span class="float-left">Email : {{userdata.email}}<a href="#" class="m-2">Edit</a></span><br/>
            <span class="float-left">Name : {{userdata.username}}<a href="#" class="m-2">Edit</a></span> -->
        </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: 'Profile',
  data(){
    return {

    }
  },
  created(){
    if(!this.token){
      this.$router.push('/login')
    }
  },
  methods:{

  },
  computed:{
      ...mapGetters(['token','userdata'])
  }
}
</script>

<style>

</style>



