<template>
    <div class="">
    <b-navbar toggleable="lg" type="dark" variant="danger" class='fixed-top'>
      <b-navbar-brand href="/" class="text-dark">Products App</b-navbar-brand>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item href="#" v-if='token'><router-link to='/dash' class="text-dark">Dashboard</router-link></b-nav-item>
          <b-nav-item href='#' v-if='!token'><router-link to='/login'>Login</router-link></b-nav-item>
          <b-nav-item-dropdown :text='userdata.username' right v-if='userdata'>
            <b-dropdown-item href="#" class=''><router-link to='/profile'>Profile</router-link></b-dropdown-item>
            <b-dropdown-item href="#" class='' @click.prevent.stop='logout'>Logout</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
        </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
name: 'Navbar',
data(){
  return {}
},
methods:{
    logout(){
      this.$store.dispatch('LOGOUT')
      this.$router.push('/')
    }},
computed:{
  ...mapGetters(['token','userdata'])
}

}
</script>

<style>

</style>



