import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)         //merge to Master

const store=new Vuex.Store({
      state:{
      userdata:JSON.parse(localStorage.getItem('user')) || '',
      token:localStorage.getItem('token') || '',
      },
    getters:{
      token:(state)=>{
        return state.token
      },
      userdata:(state)=>{
        return state.userdata
      }
    },
    mutations:{
      setUserData(state,UserData){
        state.token=UserData.api_token
        state.userdata=UserData
    },
    unsetToken(state){
      state.token=''
      state.userdata=false

    },
  },
    actions:{
      TRY_AUTH(context,authdata){
        return new Promise((resolve,reject)=>{
          window.axios.post('/login',authdata).then(resp=>{
            context.commit('setUserData',resp.data)
            localStorage.setItem('token',resp.data.api_token) //remember for tomorrow!
            let data=JSON.stringify(resp.data)
            localStorage.setItem('user',data)
            resolve('Logged in successfully !')
          }).catch((err)=>{
            reject('Wrong Email or Password')
          })
        })

        // axios.post('http://127.0.0.1:8000/api/login',authdata).then(resp=>{
        //   var userdata=resp.data       //God send us a secret key to unlock !
        //   context.commit('setUserData',userdata)
        //   localStorage.setItem('token',userdata.api_token)
        //
        // }).catch((err)=>{
        //   // alert('some error !')
      // })
      },
      LOGOUT(context){
        context.commit('unsetToken')
        localStorage.removeItem('token')   //erase from mind also
        localStorage.removeItem('user')   //erase from mind also
      }
    }
  });

export default store                //order shipped



