<template>
  <div class="card m-1">
    <!-- <div class="card m-1"> -->
    <!-- <small v-if='name_error' class='text-danger'>{{name_error}}</small> -->
      <div class="card-header rounded">
      <div class="" v-if='!name_edtng' @click.prevent.stop='name_edtng=true'>
        <span class='text-dark'>{{mproduct.name}}<a href="#" class="m-1">Edit</a></span>
      </div>
      <div class="" v-if='name_edtng'>
        <form class='form-inline' @submit.prevent.stop='updatename'>
        <input type="text" class="form-control" v-model='mproduct.name'>
        <button class="btn btn-primary btn-sm m-1" :disabled='!name_dirty'>Save</button>
        <button class="btn btn-danger m-1 btn-sm" @click.prevent.stop='name_edtng=false;name_updtng=false;name_error=false'>cancel</button>
        <img src='../assets/loading.gif' alt='loading_gif' class="img-fluid" v-if='name_updtng'>
        </form>

      </div>
    </div>

      <div class="card-body">
      <div class=""><img class="img-fluid img-thumbnail" src='../assets/logo.png' height="10%" width="10%">
        <div class=""><p v-if='!dscr_edtng' class="m-2">{{mproduct.dscr}}<a href="#" class="m-1" @click.prevent.stop='dscr_edtng=true'>Edit</a></p>
          <div class="" v-if="dscr_edtng">
            <form class="form-inline" @submit.prevent.stop='updatedscr'>
            <textarea v-model='mproduct.dscr' class="form-control m-1" cols='80' rows='5'></textarea>
            <button class="btn btn-primary m-1">Save</button>
            <img src='../assets/loading.gif' alt='loading_gif' class="img-fluid" v-if='dscr_updtng'>
          </form>
          </div>

          <span v-if='!price_edtng' class="m-1" @click.prevent.stop='price_edtng=true'>Price : {{mproduct.price}}<a href="#" class="m-1">Edit</a></span></div>
          <span v-if='price_edtng' class="m-1">Price :

            <div class="" v-if='price_edtng'>
              <form class='form-inline'>
              <input type="text" class="form-control" v-model='mproduct.price' autofocus>
              <button class="btn btn-primary m-1" @click.prevent.stop='updateprice'>Save</button>
              <img src='../assets/loading.gif' alt='loading_gif' class="img-fluid" v-if='price_updtng'>
              </form>
            </div>
            </span></div>

      <span :class="dclass.apprvd" v-if='mproduct.approved' title="Ready to sell">Approved</span>
      <span :class="dclass.napprvd" v-else title="Waiting for Approval">Pending Approval</span>



    </div>
    </div>
    </div>
  <!-- </div> -->
  </div>
</template>

<script>
export default {
  name: 'singleProduct',
  props:{product:Object},
  data(){
    return {
      mproduct:this.product,
      editlock:false,          //only one edti at a time
      name_edtng:false,
      name_updtng:false,
      name_dirty:false,    //Bandwidth is precious! changed name ? after last server update ?
      name_error:false,

      price_edtng:false,                //UI togglers
      price_updtng:false,
      price_dirty:false,

      dscr_edtng:false,
      dscr_updtng:false,
      dscr_dirty:false,
      dclass:{
        apprvd:{'badge':true,'badge-primary':true,'float-right':true},
        napprvd:{'badge':true,'badge-danger':true,'float-right':true}
      }
    }
  },
  created(){
  },
  methods:{
    updatename(){
          if(!this.editlock){
          this.editlock=true          //Lock
          this.name_updtng=true
          this.name_error=false
          axios.put('products/',this.product).then(resp=>{
          this.name_updtng=false
          this.name_edtng=false
          this.name_dirty=false
          this.editlock=false         //Release
          this.$toast.success('Success !','Name Saved Successfully',{position:'topCenter',tomeout:1000})
      }).catch(err=>{this.name_error='Name cant be updated!'
                    this.name_updtng=false
    })
    }else{return}},
    updateprice(){
          if(!this.editlock){
          this.editlock=true
          this.price_updtng=true
          axios.put('products/',this.product).then(resp=>{
          this.price_updtng=false
          this.price_edtng=false
          this.price_dirty=false
          this.editlock=false

      }).catch(err=>{this.price_updtng=false})
    }else{return}},
    updatedscr(){
            if(!this.editlock){
            this.editlock=true
            this.dscr_updtng=true
            axios.put('products/',this.product).then(resp=>{
            this.dscr_updtng=false
            this.dscr_edtng=false
            this.dscr_dirty=false
            this.editlock=false
        }).catch(err=>{this.dscr_updtng=false})
      }else{return}},
    },
    watch:{
    'product.name':function(newv,old){
        if(newv!=''){this.name_dirty=true}

       }
    }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



